html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: Inria Serif, serif;
}

h2 {
  color: #ffffffb3;
  font-size: 6.5rem;
}

header {
  clip-path: polygon(0 0, 30vw 0, 0 30vw);
  width: 100vw;
  height: 100vw;
  color: #fff;
  mix-blend-mode: multiply;
  background-color: #75135d;
  font-size: 5vw;
  position: fixed;
}

header span {
  width: 10vw;
  height: 10vw;
  border: 2px solid #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 3vw;
  display: flex;
}

section:nth-child(2n+1) {
  height: 100vh;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  justify-content: center;
  align-items: center;
  display: flex;
}

section:nth-child(2n) {
  height: 25vh;
  background-color: #f5f5f5;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 3rem;
  display: flex;
}

section p {
  color: #a2a2a2;
  width: 16%;
  text-align: left;
  padding-right: 10rem;
  font-size: 2.4rem;
}

aside {
  color: #75135db3;
  transform-origin: 0 0;
  align-self: flex-end;
  font-family: Muli, sans-serif;
  font-size: 1.7rem;
  transform: rotate(-90deg);
}

#parallax-1 {
  background-image: linear-gradient(135deg, #75135db3 31vw, #0000 31vw, #0000), url("El_Condoriri_trek.21908014.jpeg");
}

#parallax-2 {
  background-image: linear-gradient(135deg, #75135db3 31vw, #0000 31vw, #0000), url("Illiniza_Norte.c9946892.jpeg");
}

#parallax-3 {
  background-image: linear-gradient(135deg, #75135db3 31vw, #0000 31vw, #0000), url("El_Chachani_summit.112ae70b.jpeg");
}

@media only screen and (max-width: 1200px) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 1000px) {
  section:nth-child(2n) {
    height: 40vh;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    display: flex;
  }

  section p {
    max-width: 500px;
    width: 90%;
    text-align: center;
    padding-right: 0;
  }

  aside {
    transform-origin: 0 0;
    align-self: center;
    font-size: 1.7rem;
    transform: rotate(0);
  }
}

@media only screen and (max-width: 700px) {
  h2 {
    font-size: 4.4rem;
  }
}

